import React, { Component } from 'react';
class Captcha extends Component {
  constructor(props) {
    super(props);
    this.state = {
      code: null
    }
  }
  handleChange = (e) => {
    this.setState({
      [e.target.name]: e.target.value
    })
  }
  componentDidMount() {
    this.CreateCaptcha();
  }
  CreateCaptcha = (e) => {
    var code;
    document.getElementById('myCanvas').innerHTML = "";
    var charArray = "0123456789abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ@!#$%^&*";
    var captchaLength = 6
    var captcha = []
    for (var i = 0; i < captchaLength; i++) {
      //below code will not allow Repetition of Characters
      var index = Math.floor(Math.random() * charArray.length + 1); //get the next character from the array
      if (captcha.indexOf(charArray[index]) == -1)
        captcha.push(charArray[index]);
      else i--;
    }
    console.log(captcha)
    var canv = document.createElement("canvas");
    canv.id = "captcha";
    canv.width = 100;
    canv.height = 50;
    var ctx = canv.getContext("2d");
    ctx.font = "25px Broadway";
    //storing captcha so that can validate you can save it somewhere else according to your specific requirements
    // code = captcha.join("");
    this.setState({
      code: captcha.join("")
    })
    ctx.strokeText(captcha.join(""), 0, 30);
    // alert(this.state.code)
    document.getElementById("myCanvas").appendChild(canv);
  }
  ValidateCaptcha = (e) => {
    debugger
    if (this.state.code == this.state.captcha) {
      this.props.onPress()
    }
    else {
      document.getElementById("error").innerHTML = "Please enter valid Captcha"
      document.getElementById("error").style.color = "red"
      this.CreateCaptcha()
    }

  }
  render() {
    return (
      <div>
        <div id="myCanvas"></div>
        <div id="error"></div>
        <input type="text" name="captcha" className={this.props.ClassName} onChange={this.handleChange} />
        
        <div>
        <button onClick={this.ValidateCaptcha}>Click</button>
          </div>
      </div>
    )
  }
}
export default Captcha
